/* Header =================================== */
#header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100px;
  background-color: #fff;
}
#header .header_outer {
  width: 100%;
  height: 100px;
}
#header .header_box {
  display: flex;
  width: 1660px;
  height: 100px;
  margin: 0 auto;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 0.5px;
}
#header .header_box .logo {
  width: 150px;
  height: 100px;
  line-height: 105px;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
}
#header .header_box .sht_header .each_menu_ul,
#header .header_box .mainMenu .mainMenu_ul {
  width: 400px;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
}
#header .header_box .sht_header .each_menu_ul li:nth-child(4),
#header .header_box .mainMenu .mainMenu_ul li:nth-child(4) {
  width: 1px;
  height: 15px;
  border-right: 1px solid #000;
  box-sizing: border-box;
}
#header .header_box .sht_header .each_menu_ul li,
#header .header_box .mainMenu .mainMenu_ul li {
  height: 100px;
  line-height: 100px;
}
#header .header_box .sht_header .each_menu_ul a,
#header .header_box .mainMenu .mainMenu_ul a {
  color: #000;
  display: block;
}

/* GNB ========== */
#header .gnb .gnb_box .gnb_Menu {
  width: 220px;
  height: 100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
#header .gnb .gnb_box .gnb_Menu .gnb_Menu_map picture {
  padding: 5px;
